import { BlockMock, DeepPartial } from '../core'
import { Transaction } from './transaction.model'

export function TransactionMock(
  options?: DeepPartial<Transaction>
): Transaction {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'TRANSACTION_ID',
    tokenId: options?.tokenId || 'TOKEN_ID',

    vaultId: options?.vaultId || 'VAULT_ID',
    vaultAddress: options?.vaultAddress || 'VAULT_ADDRESS',
    walletId: options?.walletId || 'WALLET_ID',
    walletAddress: options?.walletAddress || 'WALLET_ADDRESS',

    type: options?.type || 'DEPOSIT',
    hash: options?.hash || 'TRANSACTION_HASH',
    block: BlockMock(options?.block),
    amount: options?.amount || '0',
    tokenAmount: options?.tokenAmount || '0',
    price: options?.price || '0',
    input: options?.input,

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
